.participants-list-container {
  flex-grow: 1;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 100%;

  .search-header-container {
    width: 100%;
    height: 60px;
    padding: 16px 16px 0 16px;
    margin-bottom: 10px;
    .search-header {
      background-color: transparent;
      height: 30px;
      border-bottom: solid 2px #107db7;
      padding-bottom: 6px;
      align-items: center;
      display: flex;
      input {
        background: transparent;
        border: none;
        padding: 0;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        color: #fff;
        width: 82%;
      }
      .search-icon {
        background-image: url("../../../assets/images/indicators/search.svg");
        background-repeat: no-repeat;
        background-position: center center;
        width: 24px;
        height: 24px;
        margin-right: 4px;
      }
      .search-icon.deactive {
        background-image: url("../../../assets/images/indicators/search_deactive.svg");
        background-repeat: no-repeat;
        background-position: center center;
        width: 24px;
        height: 24px;
        margin-right: 4px;
      }
      .mobile-chat-toggle {
        background-image: url("../../../assets/images/buttons/white_x.svg");
        margin-left: auto;
        min-width: 12px;
        min-height: 12px;
        width: 20px;
        height: 20px;
        background-size: 20px;
      }
    }
  }
  .participants-list-header {
    height: auto;
    padding: 0;
    margin: 0 16px 16px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: #8a9fb0;
    text-transform: none;
    background: none;
    border-bottom: 1px solid #8a9fb0;
    span {
      padding: 3px;
      display: inline-block;
    }
  }
  .participants-list-content {
    overflow-y: auto;
    padding-bottom: 15px;
    margin-right: 1px;
    flex-grow: 1;
  }

  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    padding: 1px 0px;
  }
  ::-webkit-scrollbar-track {
    background: transparent;
  }
  ::-webkit-scrollbar-thumb {
    background: #73879a;
    border-radius: 4px;
  }
  ::-webkit-scrollbar-thumb:horizontal:hover,
  ::-webkit-scrollbar-thumb:vertical:hover {
    background: #73879a;
    cursor: pointer;
  }
  ::-webkit-scrollbar-thumb:horizontal:active,
  ::-webkit-scrollbar-thumb:vertical:active {
    background: #73879a;
  }
}
