.tile-control.cc-moderation {
  background: url(../../../assets/images/buttons/ic_cc.svg) no-repeat center;
  background-size: 16px 16px;
}

.tile-control.cc-moderation.active {
  background: url(../../../assets/images/buttons/ic_cc_enabled.svg) no-repeat
    center;
  background-size: 16px 16px;
}

.participant-nav__icon--cc-moderation {
  background: url(../../../assets/images/buttons/ic_cc.svg) no-repeat center;
}

.participant-nav__icon--cc-moderation.active {
  background: url(../../../assets/images/buttons/ic_cc_enabled.svg) no-repeat
    center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
