.chat {
  flex: 0 0 0;
  min-height: 100%;
  height: 100%;
  overflow: hidden;
  transition: all 300ms ease-in-out;
  flex-direction: column;
  display: flex;
  .unread-message-counter {
    width: 16px;
    height: 16px;
    position: absolute;
    text-align: center;
    background-color: #009bde;
    border-radius: 16px;
    font-weight: 500;
    line-height: 16px;
    font-size: 10px;
    color: #fff;
    top: 16px;
  }
  .participant-block {
    height: 100%;
  }
  &.open {
    flex: 0 0 300px;
  }
  &.show-keyboard {
    width: 300px;
    right: 0px;
  }
  &:not(.open) > div {
    visibility: hidden;
  }
.chat-header-wrapper{
  position: sticky;
  top: 0px;
  z-index: 2;
}
  .chat-header {
    top: 0px;
    width: 100%;
    text-align: center;
    height: 48px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: white;
    padding-left: 16px;
    padding-right: 16px;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5);
    z-index: 1;
    &.show-shadow {
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      z-index: 1;
    }
    .chat-label-block{
      width: 70%;
    }
    .chat-label {
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }

    .chat-label-block {
      .chat-label-info {
        color: var(--grey, #6d767e);
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }
    }
  }
  .chat-content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background: #fff;
    min-width: 300px;
    overflow: auto;
    flex: 1;
    .history-block{
      overflow-y: scroll;
    }
    .chat-disclaimer {
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      text-align: center;
      padding: 0 16px 16px 16px;
      color: #6d767e;
    }
  }

  .chat-footer {
    display: flex;
    min-height: 64px;
    background: hsla(0, 0%, 96%, 1);
    border-top: 1px solid #e5e5e5;
    min-width: 300px;
  }

  button {
    &.mobile-chat-toggle {
      top: 12px;
      min-width: 20px;
      min-height: 20px;
      width: 24px;
      height: 24px;
      background-image: url("../../assets/images/buttons/black_x.svg");
      background-repeat: no-repeat;
      background-position: center center;
      border: none;
      background-color: #6e1e1400;
      box-shadow: none;
      background-size: 24px;
    }
    &.mobile-chat-back-to-participant-list {
      top: 12px;
      min-width: 20px;
      min-height: 20px;
      width: 24px;
      height: 24px;
      background-image: url("../../assets/images/buttons/chat-back.svg");
      background-repeat: no-repeat;
      background-position: center center;
      border: none;
      background-color: #6e1e1400;
      box-shadow: none;
      background-size: 24px;
      &:focus ,&:hover,&:active{
        background-color: #6e1e1400;
        box-shadow: none;
      }
    }
  }

  ::-webkit-scrollbar {
    width: 8px;
  }
  ::-webkit-scrollbar-track {
    background: transparent;
  }
  ::-webkit-scrollbar-thumb {
    background: #73879a;
    border-radius: 10px;
  }
  ::-webkit-scrollbar-thumb:horizontal:hover,
  ::-webkit-scrollbar-thumb:vertical:hover {
    background: #73879a;
    cursor: pointer;
  }
  ::-webkit-scrollbar-thumb:horizontal:active,
  ::-webkit-scrollbar-thumb:vertical:active {
    background: #73879a;
  }

  .chat-header.chat-participant-list {
    background: var(--darker-grey, #292c2f);
    /* Drop shadow to bottom */
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5);
    z-index: 1;
    justify-content: center;

    .chat-label {
      color: var(--white, #fff);
    }

    button.mobile-chat-toggle {
      background-image: url("../../assets/images/buttons/white_x.svg");
      position: absolute;
      right: 16px;
    }
  }
  .chat-header.group-chat-header {
    justify-content: center;

    button.mobile-chat-toggle {
      position: absolute;
      right: 16px;
    }
  }
  .participants-list-container {
    background: #292c2f;
    .participants-list-content {
      .participant-list-item {
        .person-container {
          padding: 8px 16px 8px 16px;
          .avatar {
            width: 44px;
            height: 44px;
            line-height: 44px;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            background-color: #6d767e;
          }
        }
      }
    }
  }
}

.bp5-portal:has(.chat-toaster) {
  z-index: 10004!important;
}
.chat-toaster {
  bottom: 44px !important;
  padding-right: 0 !important;
    .chat-notification:first-child:after {
      bottom: -8px;
      position: absolute;
      display: block;
      width: 16px;
      height: 16px;
      content: "";
      background-color: #e8ebed;
      transform: rotate(45deg);
      right: 27px;
    }
    .bp5-toast {
      box-shadow: none;
      background: #E8EBED;
    }
    .bp5-toast.chat-notification {
      background: #e8ebed;
      border-radius: 4px;
      transition: opacity 300ms, margin-top 300ms;
      margin-right: 8px;
      .chat-messages-notification {
        display: flex;
        .avatar {
          display: block;
          width: 44px;
          height: 44px;
          line-height: 44px;
          text-align: center;
          font-size: 17px;
          color: #fff;
          background-color: #aaa;
          border-radius: 50%;
          font-weight: 300;
          margin-right: 16px;
        }
  
        .avatar-image {
          display: block;
          width: 36px;
          height: 36px;
          line-height: 36px;
          text-align: center;
          font-size: 15px;
          color: #fff;
          background-color: #888;
          border-radius: 50%;
        }
        .chat-notification-content {
          display: flex;
          flex-direction: column;
          width: 236px;
          .participant-name {
            color: var(--dark-grey, #51575c);
            font-feature-settings: "clig" off, "liga" off;
            font-size: 14px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
          }
  
          .message-text {
            color: var(--dark-grey, #51575c);
            font-feature-settings: "clig" off, "liga" off;
            font-size: 12px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            width: 100%;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
      }
    }
  }
  



@media screen and (min-width: 480px) and (max-width: 600px){
  .chat-toaster {
      .chat-notification:first-child:after {
        right: 8px !important;
      }
    }
  
}


@media screen and (max-width: 900px) {
  .chat-toaster {
      .chat-notification:first-child:after {
        right: 16px;
      }
    }
}


@media screen and (max-width: 845px) {
  .chat {
    .chat-content {
      padding-top: 32px;
    }
    .chat-content.participant-block-chat-content {
      padding-top: 0px;
    }
    &.open {
      flex: 0 0 100%;
      button {
        &.mobile-chat-toggle {
          display: inline-block;
        }
      }
    }
    &.show-keyboard{
      width: 100%;
    }
  }
}
