.message-composer {
  display: flex;
  align-items: center;
  padding: 0 16px;
  flex: 1;
  .bp5-popover-target {
    width: 100%;
  }
  .message-box-container{
    box-shadow: none;
      box-shadow: inset 0 0 0 1px rgba($color: #fff, $alpha: 0.1);
      border: 1px solid #107db7;
      border-radius: 4px;
      width: 100%;
      display: flex;
      padding-left: 16px;
      background: white;
      height: 36px;
      align-items: center;
      &:focus-within{
        border: 2px solid #107db7;
      }   
      &.disable{
        background: rgb(211, 216, 222);
      }  
  textarea {
    resize: none;
    white-space: nowrap;
    overflow:hidden;
    box-sizing: border-box;
    padding: 0;
    &.bp5-input {
      box-shadow: none;
      width: 100%;
      -webkit-appearance: none; 
      appearance: none;
      -moz-appearance: none;
      border: none;
      outline: none;
      &::-webkit-input-placeholder {
        color: #a9b2b9;
        font-size: 14px;
      }
    }
  }

  .bp5-button.send-message {
    width: 24px;
    height: 24px;
    min-height: 24px;
    min-width: 24px;
    &,
    &:hover {
      box-shadow: none;
      background: none;
      outline: none;
      border: none;
    }
    &:active {
      background: rgba(0, 0, 0, 0.2);
    }
    &[disabled] {
      background: none;
      cursor: default;
    }
  }
}

  .participant-left-placeholder {
    width: 100%;
    text-align: center;
    color: var(--grey, #6d767e);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    span {
      font-weight: 500;
    }
  }
}
.bp5-tooltip.red-border,
.bp5-tooltip.red-border .bp5-popover-arrow::before {
  border: 1px solid #d40e00;
  border-radius: 4px;
}

.bp5-tooltip.red-border .bp5-popover-arrow {
  top: 21px !important;
}

.bp5-tooltip.red-border .bp5-popover-arrow::before {
  height: 17px;
  width: 16px;
  background-color: #e4281b;
}

.bp5-tooltip.red-border .bp5-popover-arrow .bp5-popover-arrow-border,
.bp5-tooltip.red-border .bp5-popover-arrow .bp5-popover-arrow-fill {
  fill: #e4281b;
}

.vidyo-rendering-container {
  .grid-toggle {
    &.show-keyboard{
      right: 308px !important;
    }
  }
}
