.invite-interpreter-popup-container {
  background: #17314c;
  margin: 0 30px;
  display: flex;
  flex-direction: column;
  margin-top: 24px;
  min-width: 240px;
  max-width: 450px;
  .interpreter-request-in-progress-container {
    margin-top: 24px;
  }

  .inv-room-locked {
    text-align: center;
  }
  .request-detail-data {
    color: #fff;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    margin-bottom: 34px;
  }

  .interpreter-invite-cancel-button {
    border-radius: 4px;
    background: #e42c2c;
    width: 270px;
    height: 40px;
    color: #fff;

    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    text-transform: uppercase;
    border: none;
  }
  .invite-button-container {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .request-detail-title {
    color: #fff;

    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
  }

  .interpreter-popup-title {
    text-align: center;
    color: #fff;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    padding-bottom: 8px;
  }

  .language-list-title,
  .gender-list-title {
    color: #fff;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    padding-top: 4px;
  }

  .language-list,
  .gender-list {
    .bp5-button {
      padding: 16px;
      border: 1px solid #979797;
      border-radius: 4px;
      width: 100%;
      height: 40px !important;
      color: #666;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }

  .video-option-container {
    margin-top: 4px;
    .bp5-label {
      color: #fff;
      font-size: 12px;
      font-style: normal;
      font-weight: 700;
      line-height: 20px;
    }
    .bp5-radio {
      color: #fff;

      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
    }

    .bp5-radio {
      .bp5-control-indicator {
        box-shadow: inset 0 0 0 1px #fff;
      }
    }

    label.bp5-label,
    .bp5-control {
      margin-bottom: 8px;
    }

    .bp5-control input:checked ~ .bp5-control-indicator {
      background: url("../../../../../assets/images/buttons/RadioButtonCheckedRounded.svg")
        no-repeat center;
      background-size: 20px;
      box-shadow: none;
      outline: none;
      opacity: 1;
    }
  }

  .interpreter-invite-button {
    background-color: #128bca;
    color: #fff;
    width: 270px;
    height: 40px;
    text-align: center;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    text-transform: uppercase;
  }

  .interpreter-invite-button[disabled] {
    background-color: #879197;
    pointer-events: none;
    cursor: not-allowed;
  }

  .video-option-container {
    margin-bottom: 24px;
  }
}

.language-selection-list,
.gender-selection-list {
  /* width */
  ::-webkit-scrollbar {
    width: 10px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  .bp5-popover-content {
    width: 100%;
    color: #5f6469;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 102%;
    .bp5-menu-header {
      width: calc(100% - -2px);
    }
    .bp5-menu {
      max-width: 450px !important;
      max-height: 220px;
    }
  }
}

.interpreter-invite-request-notification {
  grid-gap: 0px !important;

  img {
    width: 20px;
    height: 20px;
  }

  .content {
    max-width: 200px;
    .header {
      color: #666;
      font-size: 14px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      white-space: break-spaces;
    }

    .message {
      color: #666;

      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }

    .message-content {
      color: #666;

      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }

    .estimate-time-interpreter {
      color: #666;

      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }

  .buttons {
    display: flex !important;
    flex-direction: row;
    align-items: center;

    .button {
      min-width: 0px !important;
    }

    .cancel-interpreter-request {
      padding: 0px 0px;
      border-radius: 4px;
      background-color: #a21515 !important;
      padding: 6px !important;
      justify-content: center !important;
      align-items: center !important;
      color: #fff !important;

      text-align: center !important;
      font-size: 14px !important;
      font-style: normal !important;
      font-weight: 700 !important;
      line-height: normal !important;
      text-transform: uppercase;
      border: none;
      min-width: 95px !important;
    }

    .retry-interpreter-request {
      border-radius: 4px;
      background-color: #00c65e !important;
      padding: 6px !important;
      justify-content: center !important;
      align-items: center !important;
      color: #fff !important;

      text-align: center !important;
      font-size: 14px !important;
      font-style: normal !important;
      font-weight: 700 !important;
      line-height: normal !important;
      text-transform: uppercase;
    }

    .close-interpreter-request {
      padding: 0px 0px;
      background-image: url(../../../../../assets/images/buttons/black_x.svg);
      background-repeat: no-repeat;
      background-size: contain;
      background-color: transparent !important;
      width: 12px !important;
      height: 12px !important;
    }
  }
}
