.chat-list.participant-list-item {
  width: 100%;
  height: 56px;
  overflow: hidden;
  display: flex;

  .avatar {
    display: flex;
    width: 42px;
    height: 42px;
    line-height: 42px;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    color: #fff;
    background-color: #aaa;
    border-radius: 50%;
    font-weight: 300;
    .avatar-img {
      width: 20px;
      height: 20px;
    }
  }

  .avatar-image {
    display: block;
    width: 36px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    font-size: 15px;
    color: #fff;
    background-color: #888;
    border-radius: 50%;
  }

  .person-container {
    padding: 7px 12px 7px 16px;
    position: relative;
    .status {
      position: absolute;
      display: none;
      bottom: 8px;
      right: 8px;
      width: 12px;
      height: 12px;
      background-color: #fff;
      border-radius: 50%;
      box-shadow: 0 0 0 1px #13263a;
      transition: box-shadow 300ms;
    }
    .unread-message-counter {
      width: 16px;
      height: 16px;
      position: absolute;
      text-align: center;
      background-color: #009bde;
      border-radius: 16px;
      font-weight: 500;
      line-height: 16px;
      font-size: 10px;
      color: #fff;
      top: calc(50% - 20px);
      left: calc(100% - 30px);
    }
  }

  .details-container {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    flex-grow: 1;
    color: #fff;
    .details-column {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex-grow: 1;
      padding-right: 12px;
      .name {
        font-size: 14px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 223px;
      }

      .text {
        flex: 0 1 auto;
        display: inline-block;
        margin: 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        color: var(--light-grey, #a9b2b9);
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }

      .additional-info,
      .status-icons {
        min-width: 0;
        display: flex;
        flex-direction: row;

        .bp5-popover-wrapper {
          height: 23px;
          width: 18px;
          margin-right: 7px;
        }
      }
    }
  }
}
